import { ExecutionContext, Injectable, UnauthorizedException } from '@nestjs/common';
import { ExecutionContextHost } from '@nestjs/core/helpers/execution-context-host';
import { AuthGuard } from '@nestjs/passport';

@Injectable()
export class CryptoAuthMicroServiceGuard extends AuthGuard([ 'cryptoAuthMicroService' ]) {

  canActivate(context: ExecutionContext) {
    // console.log(context.switchToRpc().getContext());
    return super.canActivate(new ExecutionContextHost([context.switchToRpc().getContext()]));
  }

  handleRequest(err: any, user: any) {
    /* console.log("user ==>", user);
    console.log("error ==>", err); */
    if (err || !user) {
      // throw err || new AuthenticationError('GqlAuthGuard');
      throw err || new UnauthorizedException()
    }
    return user;
  }

}
